body {
  background: #fff9ff;
  font-size: 16px;
  font-family: "Noto Sans JP", sans-serif;
}

main {
  width: 1280px;
  margin: 5em auto;
  border-radius: 5px;
  background: #fffffc;
  padding: 1.5em 2em;
  min-height: calc(100vh - 13em);
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  text-align: center;
}

.card {
  background: #ffffff;
  border-radius: 5px;
  min-height: 200px;
  border: 1px solid #eeeeee;
  padding: 0 1em 1em;
  display: flex;
  flex-flow: column;
  overflow: hidden;
  margin: 1em 0;
}

.card .card-header {
  padding-top: 0.25em;
  text-align: center;
  border-bottom: 1px solid #eeeeee;
  flex: 0 0 auto;
}

.card .card-body {
  margin-top: 0.25em;
  flex: 1 0 auto;
  border-radius: 3px;
}

.container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.container .card {
  flex: 0 1 20%;
}
